import { model, Schema, models } from 'mongoose';

export default function passwordResetRequest() {
  const collectionName = 'passwordresetrequest';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const passwordResetRequestSchema = new Schema({
    userId: {
      type: String,
      required: true,
    },
    code: {
      type: String,
      required: true,
    },
    expiresAt: {
      type: Date,
      required: true,
    },
  });

  return model(collectionName, passwordResetRequestSchema);
}
